import React, { Component } from 'react'

class Banner extends Component{
  constructor(props) {
    super(props)
    this.state = {
      greeting: 'Welcome Message'
    }

    this.showGreeting = this.showGreeting.bind(this)
  }

  showGreeting() {
    console.log('click greeting')
    this.setState( () => ({greeting: 'Have a Good Time!'}))
  }
  
  render() {
    return (
      <section className="banner" onClick={this.showGreeting}>
        <h1>{this.state.greeting}</h1>
      </section>
    )
  }
}

export default Banner