import React, { Component } from 'react'

class MainContent extends Component{
	constructor(props) {
		super(props);
		this.state = {
			display: 'none'
		}
		// 為了讓 `this` 能在 callback 中被使用，這裡的綁定是必要的：
		this.showBoxes = this.showBoxes.bind(this);
	}

	showBoxes() {
		if (this.state.display === 'none') {
			this.setState(() => ({
				display: 'block'
			}))
		} else {
			this.setState(() => ({
				display: 'none'
			}))
		}
	}
	
	render() {
		return (
			<main className="main-content">
				<div className="row">
					<h2>Section Title</h2>
						<Boxes />
					<button onClick={this.showBoxes} className="btn">Call to Action</button>
				</div> 
				<div style={this.state} className="display-container">
					<div className="row">
						<Boxes />
					</div>
				</div>
			</main>
		)
	}
}

class Boxes extends Component {
	render() {
		return (
			<ul className="wrap">
				<li className="content-box">Content Box 1</li>
				<li className="content-box">Content Box 2</li>
				<li className="content-box">Content Box 3</li>
				<li className="content-box">Content Box 4</li>
			</ul>
		)
	}
}

export default MainContent