import React, { Component } from 'react'


class Nav extends Component{
  constructor(props) {
    super(props);
    this.state = {
      right: '-50%'
    }

    this.showMenu = this.showMenu.bind(this);
    this.hideMenu = this.hideMenu.bind(this);
  }

  showMenu() {
    console.log("show clicks!")
    this.setState(() => ({ right: '0'}))
  }

  hideMenu() {
    console.log("hide clicks!")
    this.setState(() => ({ right: '-50%'}))
  }


  render() {
    return (
      <div>
        
        {/* <Menu /> */}
        <section className="menu" style={this.state}>
          <span
            className="material-icons menu-clear"
            onClick={this.hideMenu}
          >
            clear
          </span>
          <ul>
            <li className="menu-item">Item 1</li>
            <li className="menu-item">Item 2</li>
            <li className="menu-item">Item 3</li>
            <li className="menu-item">Item 4</li>
          </ul>
        </section>
        
        {/* <MainHeader /> */}
          <header className="main-header">
            <p><a href="index.html">Website Title/Logo</a></p>
            <div className="main-nav-display">
              <ul className="main-nav">
                <li><a href="index.html">Item 1</a></li>
                <li><a href="index.html">Item 2</a></li>
                <li><a href="index.html">Item 3</a></li>
                <li><a href="index.html">Item 4</a></li>
              </ul>
            </div>
            <span className="material-icons main-nav-ham">
              <button onClick={this.showMenu}> menu </button>
            </span>
        </header>
        
      </div>
    )
  }
}


export default Nav